import React, { useCallback, useEffect, useRef, useState } from 'react';
import { PictureProps } from './interfaces/index';
import './picture.scss';

export const Picture = ({
  url,
  classPicture,
  loaderUrl,
  onClick,
  children,
  isBorderRadius = false,
  alt = 'atom picture',
}: PictureProps) => {
  const [loaded, setLoaded] = useState(false);
  const [className, setClassName] = useState('hidden');
  const refImg = useRef<HTMLImageElement | null>(null);
  const loadImg = useCallback(() => {
    setLoaded(true);
    setClassName('a-picture__img');
  }, []);

  const styleImg = {
    borderRadius: isBorderRadius ? '50%' : '',
  };
  useEffect(() => {
    if (refImg?.current && refImg?.current?.complete) {
      loadImg();
    }
    return () => {
      setLoaded(false);
      setClassName('hidden');
    };
  }, [refImg, loaded, loadImg, className]);

  return (
    <figure className={classPicture} onClick={() => onClick?.()}>
      {url ? (
        <img
          style={styleImg}
          ref={refImg}
          onLoad={loadImg}
          loading='lazy'
          className={className}
          src={url}
          alt={alt}
        />
      ) : (
        <article className='a-picture__icon'>{children}</article>
      )}
      {!loaded && url && (
        <div className='a-picture__loaded'>
          <img className='a-picture__loaded--img' src={loaderUrl} alt={alt} />
        </div>
      )}
    </figure>
  );
};
